body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.rules {
    border: 1px black solid;
}

.name--entry {
    display: flex;
    flex-direction: column;
    gap: 10px;
    border-radius: 5px;
    border: 3px solid white;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: black;
    width: 300px;
}

.entry {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;
    border: solid white 1px;
    border-radius: 5px;
    text-decoration: none;
    width: 70px;
    padding: 3px;
    margin-bottom: 3px;
}